.input {
  width: 100%;
  padding: 5px 20px;
  height: 40px;
  line-height: 30px;
  border-radius: 3px;
  border: 1px solid #d4d4d4;
  color:#383838;
}

.textarea {
  width: 100%;
  height: 80px;
  padding: 10px;
  line-height: 1.4;
  border: 1px solid $borderC;
  border-radius: 3px;
  resize: none;
  color: #383838;
}
.select2{
  width:100%;
}

@font-face {
  font-family: "iconfont";
  src: url('../fonts/iconfont.eot'); /* IE9*/
  src: url('../fonts/iconfont.eot#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/iconfont.woff') format('woff'), /* chrome, firefox */
  url('../fonts/iconfont.ttf') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
  url('../fonts/iconfont.svg#iconfont') format('svg'); /* iOS 4.1- */
}

.icon:after {
  font-family: "iconfont";
  font-size: 16px;
  font-style: normal;
  line-height: 1;
}

a.audio-download{
  float: left;
  padding: 0 6px;
  font-family: 'iconfont';
  color: #fff;
  font-size: 20px;
  font-style: normal;
  line-height: 36px;
}

.info-number .badge {
  position: absolute;
  left: 32px;
  top: 6px;
  padding: 2px 6px;
  border-radius: 20px;
  font-size: 11px;
  font-weight: normal;
  line-height: 13px;
}



$bg: "info" "success" "warning" "primary" "danger" "dark";
$bgC: #6bd3f3 #53d192 #ffd200 #9c78cd #e55957 #2b2b2c;

@for $i from 1 through 6 {
  .bg-#{nth($bg,$i)},
  .info-number .bg-#{nth($bg,$i)} {
    color: #fff;
    background-color: nth($bgC, $i);
  }
}

@import "autocomplete";

.preloader {
  text-align: center;
  width: 100%;
    height: 100%;
    top: 0px;
    position: fixed;
    z-index: 99999;
    background: #fff;
}

.cssload-speeding-wheel {
  position: absolute;
  top: calc(50% - 3.5px);
  left: calc(50% - 3.5px);
  width: 31px;
  height: 31px;
  margin: 0 auto;
  border: 2px solid rgba(97,100,193,0.98);
  border-radius: 50%;
  border-left-color: transparent;
  border-right-color: transparent;
  animation: cssload-spin 425ms infinite linear;
}



@keyframes cssload-spin {
  100%{ transform: rotate(360deg); transform: rotate(360deg); }
}
 