//客户详情弹层
.detail-h {
  padding: 20px 30px;
  background: #fff;
  border-bottom: 1px solid $borderC;
}

.detail-h-r {
  float: right;
}

.h-r-box {
  border: 1px solid $borderC;
  border-radius: 3px;
  @include clearfix;
}

.h-r-btn {
  padding: 0 10px;
  line-height: 30px;
  height: 30px;
  min-width: 39px;
  color: #999;
  cursor: pointer;
  &:hover {
    background: #f5f5f5;
  }
  label{
    cursor: pointer;
  }
}

.h-r-li {
  float: left;
  position: relative;
  border-left: 1px solid $borderC;
  &:first-child {
    border-left: none;
  }
}

.h-r-main {
  margin-left: 30px;
}

.h-r-icon {
  position: absolute;
  top: 2px;
  left: 6px;
}

.icon-edit {
  &:after {
    content: "\e62d";
    font-size: 27px;
  }
}

.icon-add {
  &:after {
    content: "\e640";
    font-size: 27px;
  }
}

.icon-menu {
  &:after {
    content: "\e657";
    font-size: 27px;
  }
}

.h-r-drop {
  position: absolute;
  display: none;
  z-index: 5;
  top: 33px;
  right: 0;
  left: auto;
  padding: 5px;
  min-width: 114px;
  background: #fff;
  box-shadow: 0 0 1px rgba(0, 0, 0, .5);
}

a.h-d-li, .h-d-li {
  display: block;
  padding: 10px;
  color: #333;
  text-decoration: none;
  border-top: 1px solid $borderC;
  &:hover {
    background: #f0f0f0;
  }
  &:first-child {
    border-top: none;
  }
}

.h-m-company {
  display: inline-block;
  font-weight: bold;
  line-height: 34px;
  font-size: 16px;
  vertical-align: top;
}

.qixin-btn {
  display: inline-block;
  position: relative;
  padding: 0 10px 0 30px;
  margin-left: 20px;
  height: 34px;
  line-height: 34px;
  border: 1px solid #4e606e;
  border-radius: 3px;
  background: #4e606e;
  color: #fff;
  cursor: pointer;
  i {
    display: inline-block;
    position: absolute;
    top: 4px;
    left: 3px;
    &:after {
      content: "\e648";
      font-size: 25px;
      line-height: .5;
    }
  }
}

.d-message {
  @include clearfix;
  margin-top: 20px;
}

.d-m-li {
  float: left;
  width: calc(100% / 6);
  line-height: 30px;
}

.d-m-head {
  display: inline-block;
  position: absolute;
  top: 2px;
  left: 0;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.d-m-con {
  margin-left: 33px;
}

.d-m-main {
  position: relative;
  color: #999;
  font-size: 14px;
  min-height: 30px;
}

.p-color0 {
  color: #4e606e;
}

.p-color1 {
  color: #ffb258;
}

.detail-main {
  position: absolute;
  top: 156px;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px;
  overflow-y: auto;
}

.detail-left {
  // float: left;
