.tags {
  @include clearfix;
  border-bottom: 1px solid $borderC;
}

.tags-li {
  display: inline-block;
  a {
    display: block;
    padding: 0 20px;
    line-height: 40px;
    font-size: 14px;
    color: $textC;
    text-decoration: none;
  }
  &.active {
    a {
      margin-bottom: -1px;
      box-shadow: inset 0 2px 0 #4e606e;
      border-left: 1px solid $borderC;
      border-right: 1px solid $borderC;
      border-bottom: 1px solid #fff;
    }
  }
  &:first-child.active {
    a {
      border-left: none;
    }
  }
}

.tags-main {
  min-height: 300px;
}

.tags-con-li {
  display: none;
  &.active {
    display: block;
  }
}

.list-li {
  padding: 10px;
  border-top: 1px solid $borderC;
  &:first-child {
    border-top: none;
  }
}

.title-left {
  position: relative;
  padding-left: 50px;
  margin-right: 100px;
  line-height: 40px;
  font-size: 14px;
}

