$iconVideo:(video:'\e613',download:'\e611');

@each $icon, $content in $iconVideo{
  .#{$icon}{
    @include icon;
    line-height: 0;
    &:after {
      color: #4e606e;
      content: "#{$content}";
      font-size: 23px;
      line-height: (25/23)*1;

      .table-li & {
        line-height: (30/23)*1;
      }
    }
  }
}
