.word-list {
  @include clearfix;
}

.word-li {
  float: left;
  padding: 10px 20px;
  line-height: 24px;
  color: #999;
  &:hover {
    background: #f5f5f5;
  }
}

.word-t {
  color: #39aea0;
}

.note-form {
  padding: 10px;
}

.btn-right-box {
  margin-top: 10px;
  text-align: right;
}

//时间轴
.line-box,
.line-time {
  position: relative;
  line-height: 25px;
  color: #999;
}

.line-box {
  margin: 10px;
  padding-bottom: 17px;
  &:after {
    position: absolute;
    content: "";
    width: 1px;
    top: 25px;
    bottom: 0;
    left: 15px;
    background: #d4d4d4;
  }
  &:last-child:after {
    display: none;
  }
}

.line-time {
  padding-left: 34px;
  &:before {
    content: "";
    display: inline-block;
    position: absolute;
    left: 10px;
    top: 7px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: $color;
  }
}

.line-main {
  position: relative;
  z-index: 5;
  margin-top: 10px;
  padding: 10px 10px 10px 25px;
  border-radius: 5px;
  background: #eefaf9;
}

.color {
  color: $lineColor;
}

.color-big {
  font-size: 14px;
}

.line-state {
  color: $lineColor;
}

a.line-href {
  text-decoration: underline;
  color: $lineColor;
}

.talk-time {
  .video-box {
    display: inline-block;
    position: relative;
    height:17px;
    .icon {
      position:absolute;
      top: 0;
    }
    .video {
      left: 5px;
    }
    .download {
      left: 30px;
    }
  }
}

.message-box {
  @include clearfix;
  padding: 15px;
  line-height: 28px;
  border-top: 1px dashed $borderC;
  &:first-child {
    border-top: none;
  }
}

.message-t {
  font-size: 14px;
  // font-weight: bold;
  padding: 10px;
}

.message-li {
  position:relative;
  width: 33%;
  float: left;
  padding: 2px 10px;
  margin:12px 0;
  &:hover {
    background: #f5f5f5;
  }
  &.message-row{
    width: 100%;
  }
  input,select,textarea {
    width: 85%;
    height: 28px;
    padding: 0;
    line-height: 28px;
    //min-width: 260px;
    border: 1px solid #999;
    border-radius: 3px;
    //background: none;
    //border-color:#999;
    background-color: #fff;
    color: #383838;
    box-sizing: border-box; 
    vertical-align: top;
    //&:focus{
    //  border-color:#999;
    //  background-color: #fff;
    //  color: #383838;
    //}
  }
}

.edit-icon{
  display:none;
  position:absolute;
  top:7px;
  right:14px;
  line-height: 0;
  &:after {
    color: #999;
    content: "\e61b";
    font-size: 22px;
  }
  .message-li:hover &{
    display: block;
  }
}

.message-text{
  padding-right:20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  //.message-block{
  //  display: block;
  //  border:1px solid transparent;
  //}
}
.message-name{
  float: left;
  width:100px;
}

.message-main {
  margin-left:100px;
  font-size: 14px;
  color: #c2c2c2;
}

.member {
  margin: 10px 15px;
}

.member-li {
  position: relative;
  padding: 20px 0 20px 35px;
  line-height: 26px;
  border-top: 1px solid $borderC;
  &:first-child {
    border-top: none;
  }
}


.member-head {
  position: absolute;
  left: 0;
  top: 20px;
  width: 26px;
  height: 26px;
  @include icon;
}

.change-btn {
  float: right;
  // display: inline-block;
  position: relative;
  width: 20px;
  height: 26px;
  .icon {
    top: 5px;
    left: 0;
  }
}